.weeklychart_container {
  display: flex;
  flex-direction: column;
  box-shadow: 0px 2px 3px 0px rgb(179, 182, 186);
  -webkit-box-shadow: 0px 2px 3px 0px rgb(179, 182, 186);
  -moz-box-shadow: 0px 2px 3px 0px rgb(179, 182, 186);
  height: 29.5rem;
  width: 100%;
}
.weeklychart_head {
  font-size: 1.5rem;
  font-weight: 500;
  margin: 1rem 1.5rem;
}
.weeklychart_head_sub {
  font-size: 1.1rem;
  font-weight: 400;
}
