.chart_con {
  box-shadow: 0px 2px 3px 0px rgb(179, 182, 186);
  -webkit-box-shadow: 0px 2px 3px 0px rgb(179, 182, 186);
  -moz-box-shadow: 0px 2px 3px 0px rgb(179, 182, 186);
  padding: 1rem;
  margin: 0 1rem;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}
.chart_head {
  font-size: 1.7rem;
  font-weight: 600;
  margin-bottom: 3rem;
}

@media (max-width: 1024px) {
  .chart_con {
    width: 100%;
    padding: 0;
    margin: 1rem 0;
  }
  .chart_head {
    margin-bottom: 1.5rem;
  }
}
