.payment_container {
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
    rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
  padding: 1rem;
  margin: 0 1rem;
  width: 95%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
}
.payment_header {
  display: flex;
  font-size: 1.1rem;
  font-weight: 600;
}
.total_earn {
  font-size: 1.5rem;
  font-weight: 600;
  margin-top: 0.5rem;
}
.order_status_head {
  margin: 0.3rem 0;
  font-weight: 500;
}
.order_status_head {
  margin: 0.7rem 0;
  font-weight: 500;
}
.payment_stat_card {
  display: flex;
  justify-content: space-between;
  background-color: blueviolet;
  padding: 0.6rem;
  border-radius: 0.3rem;
  margin-bottom: 0.7rem;
}
.payment_stat_head_data {
  font-weight: 500;
}
.recived {
  background-color: #ff7600;
}
.pending {
  background-color: #7952b3;
}
