.statecard_container {
  display: flex;
  gap: 1rem;
  width: 100%;
  justify-content: space-between;
}
.statecard {
  height: 7rem;
  width: 15rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-template-areas:
    "statcard_title statcard_img"
    "statcard_body statcard_img";
  background-color: rgb(154, 154, 247);
  box-shadow: 0px 2px 3px 0px rgb(152, 99, 202);
  -webkit-box-shadow: 0px 2px 3px 0px rgb(152, 99, 202);
  -moz-box-shadow: 0px 2px 3px 0px rgb(152, 99, 202);
  padding: 1rem;
  border-radius: 0.5rem;
}
.statecard:hover {
  box-shadow: 1px 4px 6px 2px rgb(152, 99, 202);
}
.statecard_title {
  grid-area: statcard_title;
  font-size: 1.3rem;
  font-weight: 700;
}
.statecard_img {
  grid-area: statcard_img;
  height: 8rem;
  width: 8rem;
}
.statecard_body {
  grid-area: statcard_body;
  font-size: 1.8rem;
  font-weight: 700;
  display: flex;
  align-items: center;
}
.order {
  background-color: rgb(154, 154, 247);
}
.monthly {
  background-color: rgb(241, 241, 158);
}
.cancel {
  background-color: rgb(240, 162, 148);
}

@media (max-width: 1024px) {
  .statecard_container {
    flex-direction: column;
    align-items: center;

    width: 100%;
  }
  .statecard {
    width: 92%;
  }
}
