.liveorder_con {
  /* height: 300px; */
  border-radius: 0.5rem;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
}
::-webkit-scrollbar-thumb {
  background: #444444;
  border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
  background: #020202;
}
.liveoredr_head {
  display: flex;
  font-weight: 600;
  font-size: 1.5rem;
  padding: 1rem 0 1rem 1rem;
}
.liveorder_table {
  overflow: auto;
  height: 220px;
  padding-bottom: 1rem;
}
.todaysorder_table {
  overflow: auto;
  height: 280px;
  padding-bottom: 1rem;
}
/* tbody tr:nth-child(odd) {
  background-color: #f2f2f2;
} */
.odrbtn {
  padding: 0.4rem;
  border-radius: 5px;
  border: none;
  margin-right: 5px;
  cursor: pointer;
}
.confirmbtn {
  background-color: rgb(48, 206, 48);
  color: white;
}
.sucess {
  background-color: rgb(48, 206, 48);
  padding: 0.5rem;
  border-radius: 5px;
}
.cancel {
  background-color: rgb(240, 162, 148);
  padding: 0.5rem;
  border-radius: 5px;
}

@media (max-width: 940px) {
  .liveorder_con {
    margin: 0 1rem 1rem 1rem;
  }
}
