.comment_container {
  background-color: #232749;
  height: 28.7rem;
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  overflow: hidden;
  margin: 0 1rem;
}
.comment_head {
  font-size: 1.2rem;
  font-weight: 600;
  color: white;
}
.comment_data {
  overflow: scroll;
  margin-top: 1rem;
  height: 25.5rem;
}
.comment_user {
  display: flex;
  background-color: rgb(154, 154, 247);
  padding: 0.4rem;
  border-radius: 0.5rem;
  align-items: center;
  margin-bottom: 0.5rem;
}
.comment_user_img_style {
  height: 3.5rem;
  width: 3.5rem;
  object-fit: cover;
  border-radius: 50%;
}
.comment_user_comment {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-left: 0.5rem;
}
.comment_customer_name {
  font-weight: 700;
}
.comment_product {
  font-weight: 500;
}
.comment_product_data {
  display: flex;
  justify-content: space-between;
}
