.dashboard {
  position: absolute;
  margin: 5rem 1rem 0rem 0;
  padding-bottom: 2rem;
  transition: 0.5s;
  transition-timing-function: ease-out;
  transition-property: all;
  display: flex;
  flex-direction: column;
}
.dashboardFull {
  margin-left: 18rem;
  width: calc(100% - 18rem);
}
.dashboardHalf {
  margin-left: 6rem;
  width: calc(100% - 6rem);
}
.box_shadow {
  box-shadow: 0px 2px 3px 0px rgb(179, 182, 186);
  -webkit-box-shadow: 0px 2px 3px 0px rgb(179, 182, 186);
  -moz-box-shadow: 0px 2px 3px 0px rgb(179, 182, 186);
}
.sec1 {
  /* display: flex;
  justify-content: space-between; */
  display: grid;
  grid-template-columns: 70% 30%;
  grid-template-rows: 35% 65%;
  gap: 0rem 0px;
  grid-template-areas:
    "cards chart_today"
    "live_order chart_today";
}
.cards {
  grid-area: cards;
  width: 100%;
  overflow: hidden;
}
.live_order {
  grid-area: live_order;
  width: 100%;
  overflow: hidden;
}
.chart_today {
  grid-area: chart_today;
  width: 100%;
  display: flex;
  overflow: hidden;
  justify-content: space-evenly;
}
.sec2 {
  display: grid;
  grid-template-columns: 70% 30%;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas: "todays_order todays_pay";
  margin-top: 2rem;
}
.todays_order {
  grid-area: todays_order;
  width: 100%;
  overflow: hidden;
}
.todays_pay {
  grid-area: todays_pay;
  width: 100%;
  display: flex;
  overflow: hidden;
  justify-content: space-evenly;
}
.sec3 {
  display: grid;
  grid-template-columns: 35% 65%;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas: "comments weekly_chart";
  margin-top: 2rem;
}
.comments {
  grid-area: comments;
  width: 100%;
}
.weekly_chart {
  grid-area: weekly_chart;
}

@media (max-width: 539px) {
  .dashboard {
    margin: 0;
    margin-top: 9rem;
    align-items: center;
    width: 100%;
  }
  .dashboardFull {
    margin: 0;
    margin-top: 9rem;
  }
  .dashboardHalf {
    margin: 0;
    margin-top: 9rem;
  }
  .sec1 {
    grid-template-columns: 100%;
    padding: 0 1rem;
    grid-template-rows: 1fr 18rem auto;
    gap: 1rem 0px;
    grid-template-areas:
      "cards"
      "live_order"
      "chart_today";
  }
  .sec2 {
    margin-top: 1rem;
    grid-template-columns: 100%;
    grid-template-rows: auto 1fr;
    gap: 0rem 0px;
    grid-template-areas:
      "todays_order"
      "todays_pay";
  }
  .sec3 {
    grid-template-columns: 100%;
    grid-template-rows: 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
      "comments"
      "weekly_chart";
  }
}
